<script type="text/javascript">
import district from "./../order/components/district.json";
import goodsSelect from "../../components/goodsSelet/select.vue";
import floatNum from "@/lib/function/floatNum";
import { createNamespacedHelpers } from "vuex";

const { mapActions } = createNamespacedHelpers("setting");

const home = require("../../assets/images/home.png");

export default {
  components: {
    goodsSelect,
  },
  data() {
    return {
      ruleForm: {
        basic_setting: {
          run_status: 0,
          restore_status: 0,
          restore_time: "",
        },
        store_setting: {
          contact: {
            phone: {
              bool: true,
              value: "",
              time: "",
            },
            online: {
              bool: false,
              time: "",
            },
            friend: {
              bool: false,
              des: "",
              time: "",
              pic_url: "",
            },
          },
          name: "",
          logo: "",
          abstract: "",
          phone: {
            time: [],
            mobile: "",
          },
          address: "",
          addressList: [],
          contact_des: "",
          contact_img: "",
        },
        trade_setting: {
          cancel_status: 1,
          cancel_time: 24,
          received_time: 7,
          evaluate_status: 1,
          evaluate_time: 60,
          after_time: 7,
          exchange_status: 1,
          pay_way: ["wechat", "alipay"],
        },
        goods_setting: {
          recommend_status: 1,
          recommend_showpage: [],
          recommend_goods: [],
          evaluate_show: 1,
          order_list_roll: 1,
          sales_show: 0,
          soldout_show: 0,
        },
        mobile_auth: [],
        userinfo_auth: [],
        share_setting: {
          cover_img: '',
          describe: ''
        }
      },
      district: [],
      crontab: "",
      rules: {
        "store_setting.name": [
          { required: true, message: "", trigger: "blur" },
        ],
        "store_setting.logo": [
          { required: true, message: "LOGO", trigger: "change" },
        ],
        "store_setting.addressList": [
          { required: true, message: "//", trigger: "change" },
        ],
        "store_setting.address": [
          { required: true, message: "", trigger: "blur" },
        ],
      },
    };
  },
  watch: {
    "ruleForm.goods_setting.recommend_goods": {
      handler(val) {
        if (val.length > 20) {
          this.ruleForm.goods_setting.recommend_goods = val.slice(0, 20);
        }
      },
    },
  },
  async mounted() {
    this.getSetting();
    this.getDistrict(district);
    this.getCrontab();
    this.district = district;
  },
  methods: {
    ...mapActions(["getSettingInfo"]),
    getSetting: function() {
      this.$heshop
        .search(
          "post", { include: "setting" }, {
            keyword: "setting_collection",
          }
        )
        .then((res) => {
          let data = res.content;

          let pay_way = [];
          for (let i in data.trade_setting.pay_way) {
            if (data.trade_setting.pay_way[i].value) {
              pay_way.push(i);
            }
          }
          data.trade_setting.pay_way = pay_way;

          let recommend_showpage = [];
          for (let i in data.goods_setting.recommend_showpage) {
            if (data.goods_setting.recommend_showpage[i].value) {
              recommend_showpage.push(i);
            }
          }
          data.goods_setting.recommend_showpage = recommend_showpage;
          data.goods_setting.recommend_goods === undefined ?
            (data.goods_setting.recommend_goods = []) :
            null;
          let new_mobile_auth = [];
          for (let i in data.user_setting.mobile_auth) {
            if (data.user_setting.mobile_auth[i].value) {
              new_mobile_auth.push(i);
            }
          }
          data.mobile_auth = new_mobile_auth;

          let new_userinfo_auth = [];
          for (let i in data.user_setting.userinfo_auth) {
            if (data.user_setting.userinfo_auth[i].value) {
              new_userinfo_auth.push(i);
            }
          }
          data.userinfo_auth = new_userinfo_auth;
          data.basic_setting.restore_time = +data.basic_setting.restore_time;
          data.store_setting.contact = data.store_setting.contact ?
            data.store_setting.contact :
            {
              phone: {
                bool: true,
                value: "",
                time: "",
              },
              online: {
                bool: false,
                time: "",
              },
              friend: {
                bool: false,
                des: "",
                time: "",
                pic_url: "",
              },
            };
          data.share_setting = data.share_setting ? data.share_setting : {
            cover_img: '',
            describe: ''
          }
          this.ruleForm = data;
        })
        .catch((error) => {
          console.error(error);
        });
    },
    getCrontab: function() {
      let _this = this;
      this.$heshop
        .crontab("post")
        .then(function(response) {
          _this.crontab = response;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    save: function() {
      let { friend, phone } = this.ruleForm.store_setting.contact;
      if (!phone.value) {
        this.$message.error("");
        return;
      }
      if (friend.bool) {
        if (!friend.pic_url) {
          this.$message.error("");
          return;
        }
      }
      this.$refs["ruleForm"].validate((valid) => {
        if (valid) {
          if (!this.ruleForm.store_setting.logo) {
            this.ruleForm.store_setting.logo =
              "https://qmxq.oss-cn-hangzhou.aliyuncs.com/home.png";
          }
          let basic_setting = JSON.parse(
            JSON.stringify(this.ruleForm.basic_setting)
          );
          let share_setting = JSON.parse(JSON.stringify(this.ruleForm.share_setting));
          if (basic_setting.restore_time) {
            let str = new Date(basic_setting.restore_time).getTime().toString();
            str.slice(0, str.length - 3);
            basic_setting.restore_time = str;
          }
          let store_setting = JSON.parse(
            JSON.stringify(this.ruleForm.store_setting)
          );
          store_setting.province = store_setting.addressList[0];
          store_setting.city = store_setting.addressList[1];
          store_setting.district = store_setting.addressList[2];
          let trade_setting = JSON.parse(
            JSON.stringify(this.ruleForm.trade_setting)
          );
          let pay_way = {
            wechat: {
              title: "",
              value: false,
            },
            alipay: {
              title: "",
              value: false,
            },
          };
          trade_setting.pay_way.forEach((item) => {
            for (let key in pay_way) {
              if (item === key) {
                pay_way[key].value = true;
              }
            }
          });
          trade_setting.pay_way = pay_way;
          let goods_setting = JSON.parse(
            JSON.stringify(this.ruleForm.goods_setting)
          );
          let recommend_showpage = {
            goodsinfo: {
              title: "",
              value: false,
            },
            pay_success: {
              title: "",
              value: false,
            },
            personal_center: {
              title: "",
              value: false,
            },
            orderinfo: {
              title: "",
              value: false,
            },
            cart: {
              title: "",
              value: false,
            },
            coupon_detail: {
              title: "",
              value: false,
            },
          };
          goods_setting.recommend_showpage.forEach((item) => {
            for (let key in recommend_showpage) {
              if (item === key) {
                recommend_showpage[key].value = true;
              }
            }
          });
          goods_setting.recommend_showpage = recommend_showpage;
          let mobile_auth = JSON.parse(
            JSON.stringify(this.ruleForm.mobile_auth)
          );
          let new_mobile_auth = {
            join_shopping_cart: {
              title: "",
              value: false,
            },
            paybuy: {
              title: "",
              value: false,
            },
            get_tickets: {
              title: "",
              value: false,
            },
          };
          mobile_auth.forEach((item) => {
            for (let key in new_mobile_auth) {
              if (item === key) {
                new_mobile_auth[key].value = true;
              }
            }
          });
          let userinfo_auth = JSON.parse(
            JSON.stringify(this.ruleForm.userinfo_auth)
          );
          let new_userinfo_auth = {
            join_shopping_cart: {
              title: "",
              value: false,
            },
            paybuy: {
              title: "",
              value: false,
            },
            get_tickets: {
              title: "",
              value: false,
            },
          };
          userinfo_auth.forEach((item) => {
            for (let key in new_userinfo_auth) {
              if (item === key) {
                new_userinfo_auth[key].value = true;
              }
            }
          });
          this.$heshop
            .setting("post", {
              keyword: "setting_collection",
              content: {
                basic_setting: basic_setting,
                store_setting: store_setting,
                trade_setting: trade_setting,
                goods_setting: goods_setting,
                user_setting: {
                  mobile_auth: new_mobile_auth,
                  userinfo_auth: new_userinfo_auth,
                },
                share_setting: share_setting
              },
            })
            .then(() => {
              this.getSettingInfo();
              this.$message.success("");
            })
            .catch((error) => {
              console.error(error);
              this.$message.error("");
            });
        } else {
          return false;
        }
      });
    },
    getDistrict: function(district) {
      for (let i = 0; i < district.length; i++) {
        district[i].label = district[i].name;
        district[i].value = district[i].name;
        if (district[i].list) {
          district[i].children = district[i].list;
          this.getDistrict(district[i].children);
        }
      }
    },
    openGoods: function() {
      this.$refs.goodsSelect.open();
    },
    reset: function() {
      let origin = window.location.origin;
      this.ruleForm.store_setting.logo = origin + "/static/images/home.png";
    },
    oninput: function(value) {
      return floatNum(value);
    },
    proving: function(val) {
      val = val.replace(/[^\.\d]/g, "");
      val = val.replace(".", "");
      return val;
    },
    copy: function(content) {
      var copyDom = document.createElement("div");
      copyDom.innerText = content;
      copyDom.style.position = "absolute";
      copyDom.style.top = "0px";
      copyDom.style.right = "-9999px";
      document.body.appendChild(copyDom);
      //
      var range = document.createRange();
      range.selectNode(copyDom);
      //
      window.getSelection().removeAllRanges();
      //
      window.getSelection().addRange(range);
      //
      var successful = document.execCommand("copy");
      copyDom.parentNode.removeChild(copyDom);
      try {
        var msg = successful ? "successful" : "failed";
        console.log("Copy command was : " + msg);
      } catch (err) {
        console.log("Oops , unable to copy!");
      }
      this.$message.success("");
    },
  },
};
</script>
