<script type="text/javascript">
import Enumerable from "linq";
import cascaderPanel from "./cascaderPanel";
export default {
    components: {
        cascaderPanel
    },
    props: {
        value: {
            type: [String, Number, Boolean, Object, Array],
            default: ''
        },
        title: {
            type: [String, Number, Boolean],
            default: ''
        }
    },
    data() {
        return {
            searchValue: "",
            dialogVisible: false,
            inputData: "",
            groupData: [],
            radio: [0, 0, 0]
        };
    },
    /**
     * 
     * @return {[type]} [description]
     */
    created() {

    },
    /**
     * 
     * @type {Object}
     */
    watch: {

    },
    /**
     * 
     * @return {[type]} [description]
     */
    async mounted() {

    },
    methods: {
        /**
         * 
         * @return {[type]} [description]
         */
        handleOpen() {
            this.$heshop.group("get", { include: 'goods' }).then(res => {
                this.dialogVisible = true;
                this.groupList = res;
                this.groupData = this.setTreeData(res);
            }).catch(error => {

            })
        },
        /**
         * 
         * @return {[type]} [description]
         */
        handleClose() {
            this.dialogVisible = false;
        },
        /**
         * 
         * @return {[type]} [description]
         */
        handleConfirm() {
            if (this.inputData.length > 0) {
                let _g = {};
                let _l = [];
                let end = this.inputData[this.inputData.length - 1];
                //
                this.inputData.map(id => {
                    for (let index in this.groupList) {
                        let item = this.groupList[index];
                        if (id == item.id) {
                            _l.push(item.name)
                        }
                        if (end == item.id) {
                            end = item;
                        }
                    }
                })
                end.address = _l.join(">");
                this.$emit('input', end);
                this.handleClose();
            } else {
                this.$message({
                    message: '',
                    type: 'warning'
                });
            }
        },
        /**
         * 
         * @return {[type]} [description]
         */
        handleClosed() {
            this.inputData = "";
        },
        /**
         * 
         * @param {[type]} data [description]
         */
        setTreeData(data) {
            //
            return this.$heshop.toTree({
                parentKey: 'parent_id',
                idKey: 'id',
                parentID: 0,
                childrenKey: 'children'
            }).on(data).get();
        },
        /**
         * 
         * @return {[type]} [description]
         */
        handleChange(e) {
            this.inputData = e;
        },
        /**
         * 
         * @param  {[type]}   queryString [description]
         * @param  {Function} cb          [description]
         * @param  {[type]}   index       [description]
         * @return {[type]}               [description]
         */
        querySearch(queryString, cb) {
            let restaurants = this.groupList;
            var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
            let _array = [];
            for (let index in results) {
                let item = results[index];
                if (item.id) {
                    //
                    let _path = item.path + '-' + item.id;
                    let _arr = _path.split('-');
                    let _name = [];
                    //0
                    _arr.shift();
                    //
                    _arr.map(id => {
                        for (let i in this.groupList) {
                            let _t = this.groupList[i];
                            if (_t.id && _t.id == id) {
                                _name.push(_t.name);
                            }
                        }
                    })
                    //
                    item.address = _name.join("/");
                    _array.push(item)
                }
            }
            //  callback 
            cb(_array || []);
        },
        /**
         * 
         * @param  {[type]} queryString [description]
         * @return {[type]}             [description]
         */
        createFilter(queryString) {
            return (restaurant) => {
                let str = ['', ...queryString, ''].join('.*'); //
                let reg = new RegExp(str) //
                return reg.test(restaurant.name) //
            };
        },
        /**
         * 
         * @return {[type]} [description]
         */
        handleSelect(item) {
            let _data = [];
            //
            let _path = item.path + '-' + item.id;
            let _arr = _path.split('-');
            let _name = [];
            this.inputData = [];
            //0
            _arr.shift();
            //
            _arr.map((id, i) => {
                if (parseInt(id) > 0) {
                    //
                    this.inputData.push(parseInt(id))
                    for (let index in this.groupList) {
                        let item = this.groupList[index];
                        if (item.id && item.id == id) {
                            _data.push(item);
                        }
                    }
                }
            })

            this.groupData = this.setTreeData(_data);
            // this.groupData = _data;
            // this.inputData = _arr;
        }
    },
    render() {
        let { dialogVisible, handleOpen, handleClose, handleConfirm, title, querySearch } = this;
        //DOMupload
        const trigger = this.$slots.trigger || this.$slots.default;

        //
        const defaultData = {
            on: {
                click: handleOpen
            }
        };
        //
        const dialogData = {
            props: {
                "visible": dialogVisible,
                "top": "10%",
                "title": title,
                "width": "800px",
                "append-to-body": true,
                "destroy-on-close": true,
                "before-close": handleClose
            },
            on: {
                "closed": this.handleClosed
            }
        };

        const AutocompleteData = {
            props: {
                "placeholder": "",
                "value-key": 'address',
                "trigger-on-focus": false,
                "fetch-suggestions": (queryString, cb) => {
                    return querySearch(queryString, cb)
                }
            },
            on: {
                select: this.handleSelect
            }
        }

        const CascaderData = {
            props: {
                "value": this.inputData,
                "options": this.groupData,
                "show-all-levels": true,
                "filterable": true,
                "key": 1,
                "props": {
                    "value": "id",
                    "label": "name"
                }
            },

        }

        //
        return <div class="group-select" {...defaultData}>
            {trigger}
            <el-dialog {...dialogData}>
                <div class="group-select">
                    <div class="group-select__search">
                        <el-autocomplete {...AutocompleteData} v-model={this.searchValue}>
                           <el-button slot="append" icon="el-icon-search"></el-button>
                        </el-autocomplete>

                    </div>
                     <cascaderPanel {...CascaderData} on-change={this.handleChange}></cascaderPanel>
                </div>
                <span slot="footer" class="dialog-footer">
                    <el-button on-click={handleClose}> </el-button>
                    <el-button type="primary" on-click={handleConfirm} on-close={handleClose}> </el-button>
                </span>
            </el-dialog>
        </div>;
    }
};
</script>
<style lang="less" scoped>
@import './style.less';
</style>